<template>
  <div class="right">
    <div class="box3">
      <label for="crimeType" class="crimeTypeLable">犯罪类型:</label>
      <select id="crimeType" name="crimeType" class="crimeTypeSelect">
        <option value="偷窃">偷窃</option>
        <option value="抢劫">抢劫</option>
        <option value="强奸">强奸</option>
        <option value="谋杀">谋杀</option>
      </select>
      <a href="" download></a>

      <span class="span1">凭证：</span><input type="text" class="upload-inputText1">
      <span class="span2">私钥：</span><input type="text" class="upload-inputText2">

      <span class="crimePosition">犯罪地点：</span><input type="text" class="upload-crimePosition">
      <span class="crimeDescript">犯罪描述：</span><input type="text" class="upload-crimeDescript">

      <input class="form-submit" type="submit" value="请求" @click="queryTr"/>
    </div>
  </div>
</template>

<script>
import Web3 from 'web3'
import axios from 'axios'
export default {
  name: 'DP-NewTr',
  data() {
    return {
    
    }
  },
  methods: {
    queryTr() {
      axios({
        url: '/api/request',
        method: 'post',
        headers: {
          token: sessionStorage.getItem('token')
        },
        data: {
          id: sessionStorage.getItem('userId'),
          cre: document.querySelector('.upload-inputText1').value,
          prk: document.querySelector('.upload-inputText2').value,
          type: document.querySelector('.crimeTypeSelect').value,
          spot: document.querySelector('.upload-crimePosition').value,
          desc: document.querySelector('.upload-crimeDescript').value
        }
      }).then(result => {
        const code = result.data.code
        if(code == 0) {
          this.$message.error('请求失败')
        } else {
          const path = result.data.data.filepath
          console.log(path)
          document.querySelector('.box3 a').href = '/api/load/' + path
          document.querySelector('.box3 a').download = path
          document.querySelector('.box3 a').click()

          try{
            this.uploadBlock({
            type: result.data.data.tx,
            para1: result.data.data.para1,
            para2: result.data.data.para2,
            ts: result.data.data.ts,
            sign: result.data.data.sign
          })
          }catch{
            this.$message.error('上传区块失败')
          }
        }
      }).catch(() => {
        this.$message.error('请求失败')
      })
    },

    uploadBlock(data) {
      const web3 = new Web3(new Web3.providers.HttpProvider("HTTP://127.0.0.1:7545"))
      web3.eth.sendTransaction({
        from: '0x24F006B75F942466DCFD24Ba80a5E53744774B4C',//如果from为空，默认取this.web3.eth.defaultAccount
        to: '0xc2cC927D5865e02c3aDBc86687f2c2fFbEBfc687',
        value: 1000000000000000, //发送的金额，这里是0.01 ether
        gas: 121000, //一个固定值，可选
        data: this.stringtoHex(JSON.stringify(data))
      }).then(function (receipt) {
        //待区块确认后会回调，通过receipt判断交易是否成功
        console.log("交易状态：", receipt.status)
      })
    },

    stringtoHex(str) {
      var val = "0x";
      for (var i = 0; i < str.length; i++) {
          if (val == "")
              val = str.charCodeAt(i).toString(16);
          else
              val += str.charCodeAt(i).toString(16);
      }
      console.log(val)
      return val
    }
  }
}
</script>

